@import '../../global.scss';

.link-btn {
    display: inline-block;
    color: rgb(211, 48, 48);
    text-decoration: none;
    background-color: transparent;
    border: 2px solid $mainColor;
    border-radius: 10px;
    padding: 5px;
    margin: auto;

    &:hover {
        font-weight: 900;
    }

}

.skills {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

   

    h1 {
        font-size: 50px;
        color: rgb(211, 48, 48);

        @include mobile {
            font-size: 20px;
        }
    }

    .container {
        width: 70%;
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile {
            flex-direction: column;
        }
    
        .card {
            width: 100px;
            height: 70px;
            border-radius: 10px;
            box-shadow: 0px 0px 15px -8px black;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            padding: 20px;
            margin: auto;
            transition: all 1s ease;

            @include mobile {
                height: 50px;
            }

         

            &.featured {
               
                margin: 0 30px;
            }

            &:hover {
                transform: scale(1.1);
            }
            
            .top {
                display: flex;
                align-items: center;
                justify-content: center;

                img {
                    &.left,
                    &.right {
                        height: 50px; 
                        padding: 10px;       
                    }
                    
                }
            }
           
    }   }

}